import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage'
import Product from '@/components/Product'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage
    },
    {
      path: '/product',
      name: 'Product',
      component: Product
    }
  ]
})



