<script>
import Firebase from 'firebase'
import toastr from 'toastr'

let config = {
  apiKey: "AIzaSyBB4u0dqPzIzwvIJ3qz7MQFfiDbjFt369g",
  authDomain: "dobraya-ovechka.firebaseapp.com",
  databaseURL: "https://dobraya-ovechka.firebaseio.com",
  projectId: "dobraya-ovechka",
  storageBucket: "dobraya-ovechka.appspot.com",
  messagingSenderId: "698251130704"
}
let app = Firebase.initializeApp(config)
let db = app.database()
let booksRef = db.ref('books')
let auth = Firebase.auth();

export default {
  name: 'app',
  firebase: {
    books: booksRef
  },
  data () {
    return {
      newBook: {
        title: '',
        author: '',
        url: 'http://'
      },
      headerNavOpened: false,
      headerFixed: false,
      headerMobileShown: true,
    }
  },
  methods: {
    login: function(e) {
      e.preventDefault();
      auth.signInWithEmailAndPassword('birmudo@gmail.com', 'dobrayaovechka')
        .then(function(res) {
          console.log('Logged in', res);
        })
        .catch(function(error) {
        var errorCode = error.code;
        var errorMessage = error.message;
        console.log(errorMessage);
      });
    },
    logout: function(e) {
      e.preventDefault();
      auth.signOut().then(function(res) {
        console.log('Logged out', res);
      }).catch(function(error) {
        console.log(error);
      });
    },
    toggleHeaderNav: function() {
      this.headerNavOpened = !this.headerNavOpened;
    },
    addBook: function() {
      booksRef.push(this.newBook);
      this.newBook.title = '';
      this.newBook.author = '';
      this.newBook.url = 'http://';
    },
    removeBook: function(book) {
      booksRef.child(book['.key']).remove()
      toastr.success('Book removed successfully')
    },
    headerBehaviourOnScroll: function(e) {
      var currentScroll = window.pageYOffset;
      if (window.pageYOffset > 70) {
        this.headerFixed = true;
      } else this.headerFixed = false;
    }
  },
  created () {
    document.addEventListener('scroll', this.headerBehaviourOnScroll)
  },
  destroyed () {
    document.removeEventListener('scroll', this.headerBehaviourOnScroll);
  },
  computed: {
    headerToggler: function () {
      return {
        'navbar-opened': this.headerNavOpened === true
      }
    },
    dateYear: function() {
      return new Date().getFullYear();
    }
  }
}
</script>


